.b-chart-content {
    position : absolute;
    width    : 100%;
    top      : 0;
    bottom   : 0;
}

.b-chart-show-controls {
    padding-top : 1em;
}

.b-chart-controls {
    position         : absolute;
    z-index          : 1;
    top              : 0;
    inset-inline-end : 0;
    gap              : 0;
}

.b-chart {
    transition : background var(--b-default-transition-duration);

    background : var(--b-neutral-100);
}
